import React, { Component, Suspense } from 'react';
import { Layout, Menu, Card, Icon, Avatar, Dropdown, Button, Select } from 'antd';
import { observable, action, toJS, computed } from 'mobx';
import { observer, inject } from 'mobx-react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import { Link, Route, Switch } from 'react-router-dom';
import hoistStatics from 'hoist-non-react-statics';
import './Home.less';
import About from './About'
import Teams from './Teams'
import Pokedex from './Pokedex'
import Resources from './Resources';

const { SubMenu } = Menu;
const { Header, Content, Footer } = Layout;
const { Option } = Select;

@observer
class Home extends Component {
  get t(){ return this.props.t; }
  get i18n(){ return this.props.i18n; }

  @observable langLabel = this.i18n.language;
  @observable currentTab = '2';

  componentWillMount() {
		let pathname = this.props.location.pathname;
		if (pathname.includes('about')) {
      this.activeMenu = '1';
      this.currentTab = '1';
		}
		if (pathname.includes('teams')) {
      this.activeMenu = '2';
      this.currentTab = '2';
    }
    if (pathname.includes('resources')) {
      this.activeMenu = '3';
      this.currentTab = '3';
		}
		// if (pathname.includes('pokedex')) {
    //   this.activeMenu = '3';
    //   this.currentTab = '3';
    // }
    // this.currentTab = '2';
  }
  
  langSwitch = async (value) => {
		// console.log(`selected ${value}`);
		this.langLabel = value;
		this.i18n.changeLanguage(value)
		// location.reload(true);
  }
  
  menuClick = (value) => {
    console.dir(value.key);
		this.currentTab = value.key;
	}

  render(){
    // console.dir(toJS(this.props));
    return (
      <div style={{height:'100vh', minWidth: 530}}>
        {/* <Layout style={{height:'100%'}}> */}
          <Header className="header">
            {/* <div style={{display:'flex'}}>
              <div style={{display:'flex',alignItems:'center'}}>
                <h2 className="cpLogo" style={{ whiteSpace: "nowrap"}}>{this.t('cedarPokemon')}</h2>
              </div>
            </div>
            <div>
            
              
            </div> */}
            <h2 className="cpLogo" style={{ whiteSpace: "nowrap"}}>{this.t('cedarPokemon')}</h2>
            <Menu
              defaultSelectedKeys={[this.activeMenu]}
              theme="light"
              mode="horizontal"
              style={{ lineHeight: '64px'}}
              >
              <Menu.Item key="1" onClick={(value)=>this.menuClick(value)}>{this.t('about')}</Menu.Item>
              <Menu.Item key="2" onClick={(value)=>this.menuClick(value)}>{this.t('teams')}</Menu.Item>
              {/* <Menu.Item key="3" onClick={(value)=>this.menuClick(value)}>{this.t('resources')}</Menu.Item> */}
              {/* <Menu.Item key="3"><Link to={'/pokedex'}>{this.t('pokedex')}</Link></Menu.Item> */}

            </Menu>
            <div style={{ display: 'flex', alignItems: 'center'}}>
              <Select defaultValue={this.langLabel} style={{ marginRight: '20px', width: '125px' }} onChange={this.langSwitch}>
								<Option value="zh-CN">中文（简体）</Option>
								<Option value="en-US">English (US)</Option>
                {/* <Option value="en-GB">English (GB)</Option>
                <Option value="ja-JP">日本語</Option> */}
							</Select>
            </div>
          </Header>
          {/* <Content style={{ padding: '0 25px', marginTop: 64,backgroundColor:'orange' }}> */}
          <Content style={{ padding: '0 25px', marginTop: 10}}>
            {/* <Switch>
              <Route path="/about" component={About} />
              <Route path="/teams" component={Teams} />
              <Route path="/pokedex" component={Pokedex} />
            </Switch> */}
            {this.currentTab=='1'&& <About/>}
            {this.currentTab=='2'&& <Teams/>}
            {this.currentTab=='3'&& <Resources/>}

          </Content>
          <Footer style={{ textAlign: 'center', background: '#fff' }}>©Project CedarNyx @2015-2019. All rights reserved.</Footer>
          {/* <Footer style={{ textAlign: 'center', background: '#fff',zIndex: '-999' }}></Footer> */}
      {/* </Layout> */}

      </div>
    );  
  }
}

export default hoistStatics(withTranslation()(Home), Home);