import React, { Component, Suspense } from 'react';
import { Card, Icon, Avatar, Timeline } from 'antd';
import { observable, action, toJS, computed } from 'mobx';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import hoistStatics from 'hoist-non-react-statics';
import yellow from './../assets/images/yellow.jpg';
import yellowAvatar from './../assets/images/yellowAvatar.jpg';
import Grid from 'antd/lib/card/Grid';
const { Meta } = Card;

class About extends Component {
  get t(){ return this.props.t; }
  get i18n(){ return this.props.i18n; }
  
  render(){

    const records = [{
      "time":"2015",
      "event":"Pokemon ORAS",
      "result":"S11:2001"
    },{
      "time":"2016",
      "event":"Pokemon ORAS",
      "result":"S16:2003"
    },{
      "time":"2016",
      "event":"Pokemon ORAS",
      "result":"S17:2006"
    },{
      "time":"2017",
      "event":"Pokemon SM",
      "result":"S3:2011"
    },{
      "time":"2019",
      "event":"Pokemon SS",
      "result":"..."
    }];


    return (
      <div style={{marginTop:'20px', display:'flex',justifyContent:'center'}}>
        {/* <Grid>
        
        </Grid> */}
        <div style={{marginTop:'20px'}}>
        <Card
          style={{ width: "300px" }}
          cover={
            <img
              alt="example"
              src={yellow}
            />
          }
          actions={[
            // <Icon type="setting" key="setting" />,
            // <Icon type="edit" key="edit" />,
            // <Icon type="ellipsis" key="ellipsis" />,
          ]}
        >
          <Meta
            avatar={<Avatar src={yellowAvatar} />}
            title="Cedar Nyx"
            description={this.t('profileDescription')}
          />
        </Card>
        <Card title={this.t('communities')} style={{ width: "300px" }}>
          <h3>{this.t('qqGroup')}1：233186435</h3>
          <h3>{this.t('qqGroup')}2：422308804</h3>
          <h3>{this.t('bilibili')}：CedarNyx</h3>
        </Card>
        
        
        {/* <div style={{margin:'10px'}}>
        </div> */}
          {/* <h4>Journey</h4>
          <Timeline>
            <Timeline.Item>2002: First played Gold/Silver </Timeline.Item>
            <Timeline.Item>2006: Fire Red</Timeline.Item>
            <Timeline.Item>2008: Pokemon Yellow</Timeline.Item>
            <Timeline.Item>2013: Pokemon XY</Timeline.Item>
            <Timeline.Item>2014: Pokemon ORAS</Timeline.Item>
            <Timeline.Item>2015: Pokemon ORAS S11:2001</Timeline.Item>
            <Timeline.Item>2016: Pokemon ORAS S16:2003 S17:2006</Timeline.Item>
            <Timeline.Item>2017: Pokemon SM S3:2011</Timeline.Item>
          </Timeline> */}
        

        </div>
          <Card title={this.t('records')} style={{ minwidth: "300px", marginTop:"20px" }}>
            {_.map(records,x=>{
              return(
                <div>
                  <Timeline>
                    <Timeline.Item>
                      <h4>{x.time}</h4>
                      <h4>{x.event}</h4>
                      <h4><b>{x.result}</b></h4>
                    </Timeline.Item>
                  </Timeline>
                </div>
              );
            })}
          </Card>
      </div>
    );  
  }
};

export default hoistStatics(withTranslation()(About), About);
