import React, { Component, Suspense } from 'react';
import { observable, action, toJS, computed } from 'mobx';
import _ from 'lodash';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import hoistStatics from 'hoist-non-react-statics';
import { Collapse, Button } from 'antd';
import pokemonGallery from './../assets/gallery/pokemonGallery.json';
import fe3hGallery from './../assets/gallery/fe3hGallery.json';
import { isImageUrl } from 'antd/lib/upload/utils';
const { Panel } = Collapse;


class Resources extends Component {
  get t(){ return this.props.t; }
  get i18n(){ return this.props.i18n; }
  render(){
    console.dir(pokemonGallery);
    return (
      <div>
          <h3>{this.t('gallery')}</h3>
          <Collapse >
            <Panel header={this.t('pokemon')}>
                {_.map(pokemonGallery, x=>{            
                    return(
                        <div style={{display:"flex",flexDirection:"column"}}>
                            <div>
                                <a href={x.videoLink}>{x.name}</a>
                                {/* <Button type="primary" icon="download" >Download</Button> */}
                            </div>
                            
                            <a href={x.imageUrl}><img style={{height:"300px",width:"480px"}} src={x.imageUrl} alt={x.imageUrl}/></a>
                        </div>
                    );
                })}
            </Panel>
          </Collapse>

          <Collapse>
            <Panel header={this.t('fireEmblem')}>
                {_.map(fe3hGallery, x=>{
                    return(
                        <div style={{display:"flex",flexDirection:"column"}}>
                            <div>
                                <a href={x.videoLink}>{x.name}</a>
                                {/* <Button type="primary" icon="download" >Download</Button> */}
                            </div>
                            
                            <a href={x.imageUrl}><img style={{height:"300px",width:"480px"}} src={x.imageUrl} alt={x.imageUrl}/></a>
                        </div>
                    );
                })}
            </Panel>
          </Collapse>
          
        
      </div>
    );  
  }
};

export default hoistStatics(withTranslation()(Resources), Resources);