import React, { Component, Suspense } from 'react';
import { observable, action, toJS, computed } from 'mobx';
import _ from 'lodash';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import hoistStatics from 'hoist-non-react-statics';
import {Koffing} from 'koffing';
import teamsData from './../assets/teams/gen7/teams.json';
import Handlebars from "handlebars";
import { Collapse } from 'antd';
const { Panel } = Collapse;

class Teams extends Component {
  get t(){ return this.props.t; }
  get i18n(){ return this.props.i18n; }

  render(){

    const teamCode = `
    === [gen7] VGC18/Temp ===

    猫老大 (Persian-Alola) (F) @ Black Glasses
    EVs: 84 HP / 172 SpD / 252 Spe
    Ability: Fur Coat
    Level: 50
    Jolly Nature
    - Fake Out
    - Foul Play
    - Parting Shot
    - Taunt
    
    O(n.n)O/ (Gengar) (F) @ Gengarite
    IVs: 0 Atk / 0 Spe
    EVs: 252 HP / 52 Def / 204 SpD
    Ability: Cursed Body
    Level: 50
    Shiny: Yes
    Sassy Nature
    - Protect
    - Ally Switch
    - Perish Song
    - Trick Room
    
    卡比兽 (Snorlax) (F) @ Wiki Berry
    IVs: 0 Spe
    EVs: 148 HP / 108 Atk / 220 Def / 28 SpD
    Ability: Gluttony
    Level: 50
    Sassy Nature
    - Protect
    - Belly Drum
    - Return
    - High Horsepower
    
    土地云 (Landorus-Therian) (M) @ Choice Band
    EVs: 196 HP / 36 Atk / 100 Def / 108 SpD / 68 Spe
    Ability: Intimidate
    Adamant Nature
    - Explosion
    - Earthquake
    - Rock Slide
    - U-turn
    
    谜拟Ｑ (Mimikyu) (F) @ Mimikium Z
    IVs: 0 Spe
    EVs: 252 HP / 252 Atk / 4 SpD
    Ability: Disguise
    Level: 50
    Brave Nature
    - Trick Room
    - Psych Up
    - Shadow Sneak
    - Play Rough
    
    卡璞・鳍鳍 (Tapu Fini) @ Figy Berry
    EVs: 244 HP / 44 Atk / 140 Def / 76 SpD
    Ability: Misty Surge
    Careful Nature
    - Protect
    - Psych Up
    - Waterfall
    - Heal Pulse
`;
    // console.log(Koffing.toJson(teamCode));

    const EVs = {
      'hp':'HP',
      'atk':'Atk',
      'def':'Def',
      'spa':'SpA',
      'spd':'SpD',
      'spe':'Spe'
    };

    Handlebars.registerHelper('ifGender', function(v1, v2) {
      return v1?'('+v1+')':'';
    });
    Handlebars.registerHelper('ifEVs', function(v1, v2) {
      return v1?'EVs:':'';
    });
    Handlebars.registerHelper('ifIVs', function(v1, v2) {
      return v1?'IVs:':'';
    });
    Handlebars.registerHelper('withEVIVs', function(v1, v2, v3) {
      return v2=='0'?' '+ v3 + ' ' + EVs[v1]:'/ '+ v3 + ' ' + EVs[v1];
    });
    
    const hbr = `
    <ul class="pokemon_list">
      {{#each pokemon}}
        <div>
          <p>{{this.name}} 
          {{ifGender this.gender}}
           @ {{this.item}}</P>
          <p>Ability: {{this.ability}}</P>
          <p>{{this.nature}} Nature</P>

          <p>{{ifEVs this.evs}}
            {{#each evs}}
              {{withEVIVs @key @index this}}
            {{/each}}
          </P>

          <p>{{ifIVs this.ivs}}
            {{#each ivs}}
              {{withEVIVs @key @index this}}
            {{/each}}
          </P>
                
          {{#each moves}}
            <p>- {{this}}</P>
          {{/each}}
          <br/>
        </div>
      {{/each}}
    </ul>
    `;
    
    const template = Handlebars.compile(hbr);

    return (
      <div>
        <h3>{this.t('gen7')}</h3>
        <div>
          <Collapse>
            {_.map(teamsData.teams, x =>{
              return(
                <Panel header={ x.folder + ' ' + x.name} key={x.name}>
                  <div dangerouslySetInnerHTML={{ __html: template(x) }} style={{lineHeight:'50%'}} />
                </Panel>
              );
            })}
          </Collapse>
        </div>
        
        
        
      </div>
    );  
  }
};

export default hoistStatics(withTranslation()(Teams), Teams);
